import { typeError } from '../utils';
import { addCondition } from './utils';

function reduce(dictionary, reducer) {
  return Object.keys(dictionary).reduce(reducer(dictionary), []);
}

function reduceConstraints(constraints, buildCondition) {
  var includes = constraints.includes,
      excludes = constraints.excludes;
  var conditions = [];

  if (includes) {
    conditions.push(buildCondition(includes));
  }

  if (excludes) {
    conditions.push(['@not', buildCondition(excludes)]);
  }

  return conditions.reduce(function (expression, condition) {
    return addCondition('@and', expression, condition);
  });
}

function transformOption(option) {
  if (option) {
    return Array.isArray(option) ? ['@includes', [option]] : ['@equal', [option]];
  }

  throw typeError('Option', option);
}

function buildCondition(key, expression) {
  return ['$>>', '@get', [key]].concat(expression);
}

function buildReducer(buildExpression) {
  return function (constraints) {
    return function (expression, key) {
      return addCondition('@and', expression, buildCondition(key, buildExpression(constraints[key])));
    };
  };
}

function buildConditionBuilder(buildExpression) {
  var reducer = buildReducer(buildExpression);
  return function (dictionary) {
    return reduce(dictionary, reducer);
  };
}

var buildOptionsExpression = buildConditionBuilder(transformOption);
var buildPeriodsExpression = buildConditionBuilder(function (period) {
  return ['@in', period];
});
export function buildExpression(event) {
  var options = event.options,
      periods = event.periods,
      rules = event.rules;
  var conditions = [];

  if (options) {
    conditions.push(reduceConstraints(options, buildOptionsExpression));
  }

  if (periods) {
    conditions.push(reduceConstraints(periods, buildPeriodsExpression));
  }

  if (rules) {
    conditions.push(reduceConstraints(rules, buildOptionsExpression));
  }

  return conditions.reduce(function (expression, condition) {
    return addCondition('@and', expression, condition);
  });
}